/* モーダル全体のスタイルを定義 */
.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: 0 auto;
  position: relative;
  min-height: 400px; /* モーダルの最小高さを設定 */
}

/* モーダルヘッダーのスタイルを定義 */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

/* モーダルタイトルのスタイルを定義 */
.modal-title {
  font-size: 24px;
  font-weight: bold;
}

/* クローズボタンのスタイルを定義 */
.close-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

/* 入力フィールドのスタイルを定義 */
.input-field {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

/* サインアップボタンのスタイルを定義 */
.signup-button {
  width: 100%;
  padding: 10px;
  background-color: #000200;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.signup-button:hover {
  background-color: #060a06;
}

/* エラーメッセージのスタイルを定義 */
.error-message {
  color: red;
  font-size: 14px;
  margin-top: 10px;
  min-height: 10px; /* エラーメッセージのためのスペースを確保 */
}